// URLeasy mock 
var baseUrl='http://tea.skycto.com/eshop'
class HTTP {
  // 
request(params) {
  // loading
  uni.showLoading({
    title: '',
  })
  // promise
  return new Promise((resolve, reject) => {
    uni.request({
      url: baseUrl + params.url,//
      method: params.method || 'GET',//get
      data: params.data || null,//
      header: {//
        'content-type': 'application/x-www-form-urlencoded'
      },
      success: res => {//loading
        uni.hideLoading({});
        resolve(res)
      },
      fail: err => {//loading
        uni.hideLoading({});
        uni.showToast({
          title: '',
          icon:"none"
        })
        reject(err)
      }
    })
  })
}
}
export {HTTP}
