let isNetWork = false;//
//
let NetWork={
	//
	On(){
		//
		uni.getNetworkType({//
			success(res) {
				// nonetrue
				if(res.networkType!=='none'){
					isNetWork=true;
					return false;
				}
				uni.showToast({
					icon:'none',
					title:''
				});
				//404
				uni.navigateTo({
					url:'/pages/404page/404page.vue'
				})
			}
		})
		//
		uni.onNetworkStatusChange((res)=>{//WiFi
			isNetWork=res.isConnected;//truefalse
			if(!res.isConnected){//app.vue
				uni.showToast({
					title:'',
					icon:'none'
				});
				//404
				uni.navigateTo({
					url:'/pages/404page/404page.vue'
				})
			}else{
				//404
				uni.navigateTo({
					url:'/pages/index/index.vue'
				})
			}
		})
	}
}
